import _ from 'lodash';
import React from 'react';
import {graphql } from 'gatsby';
import Layout from "../components/layout";
import Breadcrumb from '../components/breadcrumb';
import YearSelect from '../components/year-selector';
import GameRecord from '../components/GameRecord';
import MatchupRecord from '../components/MatchupRecord';
import { gameOutcome} from '../helpers/game';

class IndexPage extends React.Component {
	constructor(props) {
						super(props);
						this.yearChangeHandler = this.yearChangeHandler.bind(this);
						this.loadYearData = this.loadYearData.bind(this);
	        	this.state = {
							lowestScoreWin: [],
							highestScoreLoss: [],
							fewestPoints: [],
							mostPoints: [],
							topVictoryMargin: [],
							bottomVictoryMargin: [],
							topScores: [],
							bottomScores: [],
							yearLabels: [],
							YearSelected: "",
							gamesByYear: []
		        }
	        }
					
		yearChangeHandler = (event) => {
			var selected = event.target.value;
			this.setState(
				{ YearSelected: selected },
				() => this.loadYearData()
			)
		}
	
		loadYearData = () => {
			const owners = this.props.data.allDataJson.edges[0].node.owners;
			console.log(owners,"esto es owners")
			var year = this.state.YearSelected;
			var includeRegularSeason = this.refs.regular_season.checked;
			var includePlayoffs = this.refs.playoffs.checked;

			var allYearsObject = this.state.gamesByYear;
			var yearObject = this.state.gamesByYear.find(obj => obj.year === year);
			var gameWeeks = [];
			var allGamesYear = [];
			var gameStats = [];
			let allScores = [];
			var gamesByOwner = [];

			if (year === 'All') {
				allYearsObject.forEach(function (element) {
					let weeks = element.weeks;
					// Just regular season
					if (!includePlayoffs && includeRegularSeason) { 
						weeks = _.filter(weeks, { 'isPlayoff': false });
						gameWeeks.push(weeks);
					}

					//Just playoffs 
					else if (includePlayoffs && !includeRegularSeason) {
						weeks = _.filter(weeks, { 'isPlayoff': true });
						gameWeeks.push(weeks);
					}
					// All
					else if (includePlayoffs && includeRegularSeason) {
						gameWeeks.push(weeks);
					}		
				});
			}
			else {
				let weeks = yearObject.weeks;
				// Just regular season
				if (!includePlayoffs && includeRegularSeason) {
					weeks = _.filter(weeks, { 'isPlayoff': false });
					gameWeeks = weeks;
				}
				//Just playoffs 
				else if (includePlayoffs && !includeRegularSeason) {
					weeks = _.filter(weeks, { 'isPlayoff': true });
					gameWeeks = weeks;
				}
				// All
				else if (includePlayoffs && includeRegularSeason) {
					gameWeeks = weeks;
				}
			}
			

			gameWeeks.reverse();

			//get all games from weeks for each year
			if (year === 'All') {
				for (let i = 0; i < gameWeeks.length; i++) {
					gameWeeks[i].forEach((week) => {
						week.games.forEach((game) => {
							allGamesYear.push(game);
							console.log({game})
							console.log( game.team_a["0"].ownerId, " game.team_a[].ownerId")
							console.log( game.team_b["0"].ownerId, " game.team_b[].ownerId")
							allScores.push({
								id: game.team_a["0"].ownerId,
								name: _.find(owners, ['id', game.team_a["0"].ownerId]).name,
								score: game.team_a["0"].score,
								week: game.week,
								year: game.year,
								image: _.find(owners, ['id', game.team_a["0"].ownerId]).image
							});
							allScores.push({
								id: game.team_b["0"].ownerId,
								name: _.find(owners, ['id', game.team_b["0"].ownerId]).name,
								score: game.team_b["0"].score,
								week: game.week,
								year: game.year,
								image: _.find(owners, ['id', game.team_b["0"].ownerId]).image
							});
						})
					})
				}
			}
			// Get games for just one year
			else {
				gameWeeks.forEach((week) => {
					week.games.forEach((game) => {
						allGamesYear.push(game);
						allScores.push({
							id: game.team_a["0"].ownerId,
							name: _.find(owners, ['id', game.team_a["0"].ownerId]).name,
							score: game.team_a["0"].score,
							week: game.week,
							year: game.year,
							image: _.find(owners, ['id', game.team_a["0"].ownerId]).image
						});
						allScores.push({
							id: game.team_b["0"].ownerId,
							name: _.find(owners, ['id', game.team_b["0"].ownerId ]).name,
							score: game.team_b["0"].score,
							week: game.week,
							year: game.year,
							image: _.find(owners, ['id', game.team_b["0"].ownerId]).image
						});
					})
				})
			}

			//get games by Owner
			var teamObject = []
			if(year === 'All') {
				allGamesYear.forEach((game) => {
					if (gamesByOwner.find(obj => obj.id === game.team_a["0"].ownerId)) {
						teamObject = gamesByOwner.find(obj => obj.id === game.team_a["0"].ownerId);
							teamObject.games.push(game);
					} else {
						gamesByOwner.push({
							id: game.team_a["0"].ownerId,
							name: game.team_a["0"].owner,
							games: [game]
						})
					}
					if (gamesByOwner.find(obj => obj.id === game.team_b["0"].ownerId)) {
						teamObject = gamesByOwner.find(obj => obj.id === game.team_b["0"].ownerId);
							teamObject.games.push(game);
					} else {
						gamesByOwner.push({
							id: game.team_b["0"].ownerId,
							name: game.team_b["0"].owner,
							games: [game]
						})
					}
				})
			} else { 
				allGamesYear.forEach((game) => {
				if(game.team_a["0"].name) {
					if (gamesByOwner.find(obj => obj.id === game.team_a["0"].ownerId)) {
						teamObject = gamesByOwner.find(obj => obj.id === game.team_a["0"].ownerId);
							teamObject.games.push(game);
					} else {
						gamesByOwner.push({
							id: game.team_a["0"].ownerId,
							name: game.team_a["0"].name,
							games: [game]
						})
					}
				} 
				
				if(game.team_b["0"].name) {
					if (gamesByOwner.find(obj => obj.id === game.team_b["0"].ownerId)) {
						teamObject = gamesByOwner.find(obj => obj.id === game.team_b["0"].ownerId);
							teamObject.games.push(game);
					} else {
						gamesByOwner.push({
							id: game.team_b["0"].ownerId,
							name: game.team_b["0"].name,
							games: [game]
						})
					}
				} 
			})
			}

			allGamesYear.forEach((game) => {
				gameStats.push(gameOutcome(game,owners));
			});


			allScores = _.sortBy(allScores, ['score']);
			var topScores = _.orderBy(_.takeRight(allScores, 10), ['score'], ['desc']);
			var bottomScores = _.take(allScores, 10);

			gameStats = _.compact(gameStats);
			var topVictoryMargin = _.orderBy(_.takeRight(_.sortBy(gameStats, ['margin']), 10), ['margin'], ['desc']);
			var bottomVictoryMargin = _.take(_.sortBy(gameStats, ['margin']), 10);

			var mostPoints = _.orderBy(_.takeRight(_.sortBy(gameStats, ['sumScore']), 10), ['sumScore'], ['desc']);
			var fewestPoints = _.take(_.sortBy(gameStats, ['sumScore']), 10);

			var highestScoreLoss = _.orderBy(_.takeRight(_.sortBy(gameStats, ['scoreLose']), 10), ['scoreLose'], ['desc']);
			var lowestScoreWin = _.take(_.sortBy(gameStats, ['scoreWin']), 10);

			
			this.setState({ lowestScoreWin: lowestScoreWin });
			this.setState({ highestScoreLoss: highestScoreLoss });
			this.setState({ fewestPoints: fewestPoints });
			this.setState({ mostPoints: mostPoints });
			this.setState({ topVictoryMargin: topVictoryMargin });
			this.setState({ bottomVictoryMargin: bottomVictoryMargin });
			this.setState({ topScores: topScores });
			this.setState({ bottomScores: bottomScores });
			
		}
	
		UNSAFE_componentWillMount() {
			const years = this.props.data.allDataJson.edges[0].node.years;
			var games = [];
			
			years.forEach((year) => {
				year.weeks_games.forEach((week) => {
					games.push(...week.games);
				})
			});

			var playOffWeeks = [];
			var gamesByYear = [];
			var yearLabels = [];
			
			// get games by years and weeks

			games.forEach((game) => {
				// If year repeats itself
				if(gamesByYear.find(obj => obj.year === game.year)) { 
					var currentYear = gamesByYear.find(obj => obj.year === game.year)
						if (currentYear.weeks.find(obj => obj.week === game.week)) {
							var currentWeek = currentYear.weeks.find(obj => obj.week === game.week)
							currentWeek.games.push(game);
						} else {
							currentYear.weeks.push({
								week: game.week,
								games: [game]
							})
						}
				} else { // Initial year 
					gamesByYear.push({
							year: game.year,
							weeks: [{
								week: game.week,
								games: [game]
							}]
					});
					yearLabels.push(game.year);
				}
			});

			// mark the playoff weeks and populate array
			gamesByYear.forEach((year) => {
				year.weeks.forEach((week) => {
					if (week.games[0].playoffTierType !== 'NONE') {
						week.isPlayoff = true;
						playOffWeeks.push(week)
					}
					else {
						week.isPlayoff = false;
					}
				});
			}); 
			
			yearLabels.reverse();

			this.setState({yearLabels:yearLabels });
		
			this.setState({gamesByYear:gamesByYear})
			this.setState({YearSelected: 'All' })
			 
  	}        
  	
		componentDidMount() {
			this.loadYearData();
		}
			
		regularSeasonClickHandler = event => {
			this.loadYearData();
		}
		
  	render() {
	
		 return (
			 <Layout>
		   <main className="container-fluid">
		    <Breadcrumb title="Game Records"/>
				 <div className="card mb-3">
			      <div className="card-header"> 
							<input type="checkbox" id="regular-season" ref="regular_season" value="regular-season" onChange={this.regularSeasonClickHandler} defaultChecked={true} /> <span className="checkbox-label">Regular Season</span>
							<input type="checkbox" id="playoffs" ref="playoffs" value="playoffs" onChange={this.regularSeasonClickHandler} /> <span className="checkbox-label">Playoffs</span>
							<br/><br/>
							<YearSelect data={this.state.yearLabels} change={(event) => this.yearChangeHandler(event)}/>
			      </div>
						<div className="card-body">
							<div className="row">
								<GameRecord data={this.state.topScores} label="Score" title="Top Scores"  />
								<GameRecord data={this.state.bottomScores} label="Score" title="Lowest Scores" />
							</div>
						 	<div className="row my-4">
							 <MatchupRecord data={this.state.topVictoryMargin} label="Margin" show="Margin" title="Highest Victory Margins" />
							 <MatchupRecord data={this.state.bottomVictoryMargin} label="Margin" show="Margin" title="Lowest Victory Margins" />
						 	</div>
							<div className="row my-4">
							 <MatchupRecord data={this.state.mostPoints} label="Points" show="Points" title="Most Combined Points" />
							 <MatchupRecord data={this.state.fewestPoints} label="Points" show="Points" title="Fewest Combined Points" />
							</div>
							<div className="row my-4">
							 <MatchupRecord data={this.state.highestScoreLoss} label="Score" show="scoreLose"  title="Highest Score in a Loss" />
							 <MatchupRecord data={this.state.lowestScoreWin} label="Score" show="scoreWin" title="Lowest Score in a Win" />
							</div>
			      </div>
						<div className="card-footer small text-muted"></div>
			    </div>
		   </main>
			 </Layout>
			 );
	}
}
export default IndexPage;

export const pageQuery = graphql`
  query gameRecordsQuery {
		allDataJson {
			edges {
				node {
					owners {
						name
						fantasyname
						id
						image
						status
					}
					years{
						weeks_games {
							games {
								week
								playoffTierType
								team_a {
									name
									owner
									ownerId
									logo
									score
									rank
								}
								team_b {
									name
									owner
									ownerId
									logo
									score
									rank
								}
								year
							}
						}
					}
				} 
			}
		}
}
`;
